import { DomUtils } from "@devexpress/utils/lib/utils/dom";
import { getElementOffsetWidth, getLeftRightMargins } from "../dom-utils";
import { EllipsisBtnBlock, TitleBlock } from "./blocks";
import { BlockSize } from "./layout-update-service";
import { ToolbarCssClasses } from "./toolbar-css-classes";
export const globalSkipParentMargin = false;
export const groupSelectorString = "data-dxtoolbar-group-id";
export const titleSelectorString = "data-dxtoolbar-title-id";
export const ellipsisSelectorString = "data-dxtoolbar-ellipsis-id";
export const btnToolbarSelector = ".dxbl-btn-toolbar";
export const imageNotToggleSelector = ".dxbl-image:not(.dxbl-toolbar-dropdown-toggle)";
export const imageToggleSelector = ".dxbl-image.dxbl-toolbar-dropdown-toggle";
export const toolbarTemplateSelector = ".dxbl-toolbar-item-tmpl";
export const groupSeparatorSelector = ".dxbl-toolbar-group-separator";
export const toolbarItemTagName = "DXBL-TOOLBAR-ITEM";
export const dataToolbarIdAttribute = "data-dxtoolbar-id";
// TODO: find a way to get this from element on prerender
const separatorWidth = 1;
export function getNodeWidth(node, skipMargins = false, skipParentMargins = false) {
    if (!node)
        return 0;
    let elementWidth = Math.ceil(getElementOffsetWidth(node) + (skipMargins ? 0 : getBoxOuterOffset(node)));
    if (node.parentElement && !skipParentMargins) {
        const separator = [...node.parentElement.children].find(x => x.classList.contains(ToolbarCssClasses.ToolbarGroupSeparator));
        if (node.parentElement.lastElementChild === node) {
            const parentStyle = DomUtils.getCurrentStyle(node.parentElement);
            elementWidth += DomUtils.pxToInt(parentStyle.marginRight);
        }
        if (node.parentElement.firstElementChild === separator && separator.nextElementSibling === node) {
            const separatorElement = separator;
            if (separatorElement) {
                const parentStyle = DomUtils.getCurrentStyle(node.parentElement);
                const separatorStyle = DomUtils.getCurrentStyle(separatorElement);
                elementWidth += separatorElement.offsetWidth !== 0 ? separatorElement.offsetWidth : separatorWidth +
                    DomUtils.pxToInt(parentStyle.marginLeft) +
                    DomUtils.pxToInt(separatorStyle.marginRight);
            }
        }
        if (node.parentElement.firstElementChild === node) {
            const parentStyle = DomUtils.getCurrentStyle(node.parentElement);
            elementWidth += DomUtils.pxToInt(parentStyle.marginLeft);
        }
    }
    return elementWidth;
}
export function getItemWithoutTextWidth(itemEl) {
    let width = getNodeWidth(itemEl, false, globalSkipParentMargin);
    if (width === 0)
        return width;
    if (itemEl) {
        const template = itemEl.querySelector(toolbarTemplateSelector);
        if (template)
            return width;
        const notToggleImage = itemEl.querySelector(imageNotToggleSelector);
        if (notToggleImage) {
            const toggleImage = itemEl.querySelector(imageToggleSelector);
            width -= getBoxOuterOffset(notToggleImage);
            let nodeToHide = notToggleImage;
            while (nodeToHide = nodeToHide.nextElementSibling) {
                if (!isPopupElement(nodeToHide) && isPositionAbsolute(nodeToHide) && nodeToHide !== toggleImage)
                    width -= getNodeWidth(nodeToHide);
            }
        }
    }
    return width;
}
export function getItemSizeAffectingProperties(b) {
    return [b.item.text, b.item.isDisplayed, b.item.iconCssClass]; // todo: itemsize //todo: renderMode
}
export function getSystemItemSizeAffectingProperties(_b) {
    return [ /* b.item.Text, b.item.IconCssClass*/];
}
export function getBoxOuterOffset(el) { return getLeftRightMargins(el); }
export function isPopupElement(el) { return DomUtils.hasClassName(el, "popout"); }
function isPositionAbsolute(element) { return DomUtils.getCurrentStyle(element).position !== "absolute"; }
export function getSelector(selectorString, id) {
    return `[${selectorString}=${id}]`;
}
export function getAdaptiveItemSelector(id) {
    return `#${id}[dxbl-toolbar-adaptive-item]`;
}
export function areEqual(array1, array2) {
    return (array1.length === array2.length) && array1.every((element, index) => element === array2[index]);
}
export function toBlockSize(blocks, layer) {
    return [...new Set(blocks)].map(lb => new BlockSize(lb.block.id, lb.block.index, lb.block.adaptivePriority, lb.getMinWidth(), lb.getMaxWidth(layer), lb.blockCollection.margins, lb.block instanceof TitleBlock, lb.block instanceof EllipsisBtnBlock));
}
export function totalMargin(layoutBlock) {
    return [...new Set(layoutBlock.map(b => b.blockCollection))]
        .map(b => b.margins).reduce((pv, cv) => pv + cv, 0);
}
export function getEnumValues(enumObject) {
    return Object.keys(enumObject)
        .filter(key => Number.isNaN(Number(key)))
        .map(key => enumObject[key]);
}
