import { LayerType } from "./layouts";
// TODO: obsolete, delete after layouts.ts refactoring
export class LayoutRangeCalculator {
    constructor() {
        this.rangeCalcDict = new Map();
        this.rangeCalcDict.set(LayerType.Default, new DefaultLayerRangeCalculator());
        this.rangeCalcDict.set(LayerType.FullSequential, new SequentialLayerRangeCalculator());
        this.rangeCalcDict.set(LayerType.NoText, new SimultaneousLayerRangeCalculator());
        this.rangeCalcDict.set(LayerType.NoTextSequential, new SequentialLayerRangeCalculator());
    }
    getRange(layerType, layers) {
        const layer = layers[layerType];
        const calculator = this.rangeCalcDict.get(layerType);
        const prevBlocks = [];
    }
}
export class LayerRangeCalculator {
    getMin(blocks) {
        return blocks.map(x => x.minSize).reduce((pv, cv) => pv + cv);
    }
    getMax(blocks) {
        return blocks.map(x => x.fullSize).reduce((pv, cv) => pv + cv);
    }
}
export class DefaultLayerRangeCalculator extends LayerRangeCalculator {
    getRange(blocks, margin, prevLayerBlocks) {
        return {
            min: this.getMin(blocks) + margin,
            max: this.getMax(blocks) + margin,
        };
    }
}
export class SequentialLayerRangeCalculator extends LayerRangeCalculator {
    getRange(blocks, margin, prevLayerBlocks) {
        return {
            min: this.getMin(blocks),
            max: this.getMax(blocks) + margin,
        };
    }
}
export class SimultaneousLayerRangeCalculator extends LayerRangeCalculator {
    constructor() {
        super(...arguments);
        this.prevLayerRangeCalculator = new SequentialLayerRangeCalculator();
    }
    // TODO: accurate this calculation - keep in mind getPrevLayerRangeMax calc
    getRange(blocks, margin, prevLayerBlocks = []) {
        return {
            min: this.getMin(blocks) + margin,
            max: this.getMax(prevLayerBlocks) + margin - 1
        };
    }
}
